/**
 *        
 */
class GeometryValidator {
    /**
     *      NaN    position 
     * 
     * @param {THREE.BufferGeometry} geometry  
     * @returns {boolean} true    , false   
     */
    static validatePositionAttribute(geometry) {
      if (!geometry || !geometry.attributes || !geometry.attributes.position) {
        console.warn('GeometryValidator:     position', geometry);
        return false;
      }
  
      const positionAttr = geometry.attributes.position;
      const positions = positionAttr.array;
      let hasNaN = false;
      let fixedCount = 0;
  
      //   NaN 
      for (let i = 0; i < positions.length; i++) {
        if (isNaN(positions[i])) {
          hasNaN = true;
          //  NaN  0 (   )
          positions[i] = 0;
          fixedCount++;
        }
      }
  
      if (hasNaN) {
        console.warn(`GeometryValidator:  ${fixedCount} NaN   `, geometry);
        positionAttr.needsUpdate = true;
        geometry.computeBoundingSphere();
        geometry.computeBoundingBox();
        return true;
      }
  
      return false;
    }
  
    /**
     * ,         
     * 
     * @param {THREE.BufferGeometry} geometry  
     * @returns {boolean} true   
     */
    static isValid(geometry) {
      if (!geometry || !geometry.attributes || !geometry.attributes.position) {
        return false;
      }
  
      const positionAttr = geometry.attributes.position;
      const positions = positionAttr.array;
  
      for (let i = 0; i < positions.length; i++) {
        if (isNaN(positions[i]) || !isFinite(positions[i])) {
          return false;
        }
      }
  
      return true;
    }
  
    /**
     *     ,    
     * 
     * @param {THREE.Object3D} sceneObject   
     * @returns {number}   
     */
    static validateScene(sceneObject) {
      if (!sceneObject) return 0;
      
      let fixedCount = 0;
      
      sceneObject.traverse((object) => {
        if (object.geometry) {
          if (this.validatePositionAttribute(object.geometry)) {
            fixedCount++;
          }
        }
      });
      
      return fixedCount;
    }
  }
  
  //  
  export { GeometryValidator };